
#shopdetail {
  background: #f0f0f0;
  width: 10.8rem;
  overflow-x: hidden;
}
#shopdetail .album {
  position: relative;
  width: 100%;
  height: 5rem;
  background-color: #f0f0f0;
  background-size: contain;
  text-align: center;
  line-height: 200px;
  overflow: hidden;
}
#shopdetail .album .header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 1.48rem;
  width: 10.8rem;
  line-height: 1.48rem;
}
#shopdetail .album .header :nth-child(1) {
  float: left;
  color: #fff;
  margin-left: 0.3rem;
  margin-top: 0.15rem;
  width: 1.28rem;
  height: 1.28rem;
  line-height: 1.35rem;
  background: rgba(62,62,62,0.459);
  border-radius: 50%;
  font-size: 0.7rem;
  font-weight: bold;
}
#shopdetail .album .header :nth-child(2) span {
  float: right;
  color: #fff;
  margin-right: 0.3rem;
  margin-top: 0.15rem;
  width: 1.28rem;
  height: 1.28rem;
  line-height: 1.5rem;
  background: rgba(62,62,62,0.459);
  border-radius: 50%;
  text-align: center;
}
#shopdetail .album .header :nth-child(2) .icon-collect {
  font-size: 0.7rem;
  font-weight: bold;
  color: #fff;
}
#shopdetail .album .header :nth-child(2) .icon-collect-solid {
  font-size: 0.6rem;
  font-weight: bold;
  color: #ffda62;
}
#shopdetail .vipcard {
  margin-top: 0.1rem;
  line-height: 0.8rem;
  height: 2rem;
  position: relative;
  background: #fcf5e3;
}
#shopdetail .vipcard .left {
  position: absolute;
  left: 0.8rem;
  top: 0.2rem;
}
#shopdetail .vipcard .left .logo {
  color: #da9424;
  font-size: 0.46rem;
  font-weight: bold;
}
#shopdetail .vipcard .left .vipinfo {
  color: #9c9b98;
}
#shopdetail .vipcard .right {
  position: absolute;
  top: 0.5rem;
  right: 0.7rem;
  height: 1rem;
  width: 2.8rem;
  text-align: center;
  line-height: 1rem;
  background: #ff621a;
  border-radius: 0.5rem;
  color: #fff;
}
#shopdetail .vipcard .right .icon-spinner8 {
  position: absolute;
  top: 0.28rem;
  left: 1.2rem;
  font-size: 0.46rem;
}
#shopdetail .vipcard .right .rotate-enter-active {
  transition: -webkit-transform 4s linear;
  transition: transform 4s linear;
  transition: transform 4s linear, -webkit-transform 4s linear;
}
#shopdetail .vipcard .right .rotate-enter {
  -webkit-transform: rotate(-1440deg);
          transform: rotate(-1440deg);
}
#shopdetail .mask {
  position: fixed;
  height: 100%;
  width: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
  background: rgba(93,93,93,0.3);
  z-index: 1;
}
#shopdetail .mask .toCard {
  display: inline-block;
  margin-top: calc((100% - 6rem) / 2);
  margin-left: 2.5rem;
  text-align: center;
  height: 6rem;
  width: 5.8rem;
  background: #fff;
  border-radius: 0.2rem;
}
#shopdetail .shopCoupon {
  margin-top: 0.1rem;
  line-height: 1rem;
  background: #fff;
}
#shopdetail .name {
  margin: 0rem auto;
  height: 1rem;
  width: 4em;
  color: #525252;
  font-size: 0.5rem;
}
#shopdetail .text {
  height: 1.3rem;
  width: 10em;
  margin: 0 auto;
  font-size: 0.28rem;
}
#shopdetail .shopInfoWrapper {
  padding: 0.3rem 0.5rem;
  margin-top: 0.1rem;
  background: #fff;
}
#shopdetail .shopInfoWrapper .shopName {
  font-size: 0.66rem;
  padding: 0.3rem 0.1rem 0.5rem 0.3rem;
  margin-bottom: 0.5rem;
  border-bottom: 1px solid rgba(222,222,222,0.871);
}
#shopdetail .shopInfoWrapper .shopName .popularity {
  line-height: 0.8rem;
  float: right;
  font-size: 0.32rem;
  color: #f64f48;
}
#shopdetail .shopInfoWrapper .shopInfo {
  padding: 0.3rem 0.3rem;
  font-size: 0.4rem;
  color: #525252;
  position: relative;
}
#shopdetail .shopInfoWrapper .left {
  line-height: 0.7rem;
}
#shopdetail .shopInfoWrapper .left .clock {
  position: relative;
}
#shopdetail .shopInfoWrapper .left .clock .icon {
  position: absolute;
  display: inline-block;
  top: calc((100% - 0.5rem) / 2);
  width: 1rem;
}
#shopdetail .shopInfoWrapper .left .clock .clocktext {
  display: inline-block;
  width: 5.5rem;
  margin: auto 0;
  margin-left: 1rem;
}
#shopdetail .shopInfoWrapper .left .location {
  position: relative;
  margin-top: 0.18rem;
}
#shopdetail .shopInfoWrapper .left .location .icon {
  position: absolute;
  display: inline-block;
  top: calc((100% - 0.5rem) / 2);
  width: 1rem;
}
#shopdetail .shopInfoWrapper .left .location .locationtext {
  left: 1rem;
  width: 5.8rem;
  margin: auto 0;
  margin-left: 1rem;
  display: inline-block;
}
#shopdetail .shopInfoWrapper .right {
  position: absolute;
  right: 1.1rem;
  top: calc((100% - 0.5rem) / 2);
}
#shopdetail .shopInfoWrapper .right .content p {
  display: inline-block;
  padding-left: 0.9rem;
  border-left: solid 1px #343434;
}
#shopdetail .shopdetail {
  margin-top: 0.1rem;
  background: #fff;
  font-size: 0.32rem;
  color: #525252;
}
#shopdetail .shopdetail .name {
  margin: 0rem auto;
  height: 1.3rem;
  line-height: 1.3rem;
  width: 5em;
  color: #525252;
  font-size: 0.5rem;
}
#shopdetail .shopdetail .text {
  height: 1.3rem;
  width: 9em;
  margin: 0 auto;
  font-size: 0.28rem;
}
#shopdetail .shopdetail .content {
  font-size: 0.44rem;
  line-height: 0.63rem;
  padding: 0.2rem 0.69rem;
  text-indent: 2em;
}


