
#payresult {
  position: absolute;
  width: 100%;
  height: 100%;
}
#payresult .iconv {
  margin-top: 2.5rem;
  width: 100%;
  display: block;
  text-align: center;
  font-size: 2.5rem;
}
#payresult .text {
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 1rem;
  font-size: 0.7rem;
}
#payresult .textname {
  margin-top: 2rem;
  color: #09bb07;
  font-size: 0.48rem;
}
#payresult .textmoney {
  margin-top: 0.5rem;
  font-weight: bold;
  font-size: 0.78rem;
}
#payresult .texttime {
  margin-top: 0.5rem;
  font-size: 0.48rem;
  margin-bottom: 3rem;
}
#payresult .tip {
  text-align: center;
  line-height: 0.8rem;
}


