
#indexCouponList {
  border-bottom: 2px solid #f0f0f0;
}
#indexCouponList .couponItem {
  position: relative;
  background: #fff;
  height: 2.8rem;
  width: 100%;
  padding-left: 0.24rem;
}
#indexCouponList .couponItem .img {
  position: absolute;
  top: 0.1rem;
  left: 0.2rem;
  height: 2.55rem;
  width: 2.55rem;
  overflow: hidden;
  border-radius: 6%;
}
#indexCouponList .couponItem .img img {
  height: 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-origin: content-box;
}
#indexCouponList .couponItem .text {
  position: absolute;
  top: 0.2rem;
  left: 3.03rem;
  display: inline-block;
  margin-left: 0.1rem;
  height: 2.75rem;
  width: 7rem;
  font-size: 0.5rem;
}
#indexCouponList .couponItem .text .shopName {
  margin-top: 0.1rem;
  font-weight: 500;
  font-size: 0.5rem;
  color: #212121;
  line-height: 0.6667rem;
}
#indexCouponList .couponItem .text .shopName span {
  font-size: 0.6rem;
}
#indexCouponList .couponItem .text .couponType {
  margin-top: 0.35rem;
  height: 0.4rem;
  color: #525252;
}
#indexCouponList .couponItem .text .couponType span {
  display: inline-block;
  font-size: 0.3rem;
}
#indexCouponList .couponItem .text .couponType span span {
  margin-right: 0.1rem;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  text-align: center;
  font-size: 0.3rem;
  background: #cecece;
  color: #fff;
  border-radius: 0.08rem;
}
#indexCouponList .couponItem .text .couponType span .experience {
  background: #38e237;
}
#indexCouponList .couponItem .text .couponType span .instead {
  background: #f64f48;
}
#indexCouponList .couponItem .text .couponType span .discount {
  background: #ff9c00;
}
#indexCouponList .couponItem .text .couponType span .function {
  background: #be61b2;
}
#indexCouponList .couponItem .text .couponType span .price {
  border: none;
  width: auto;
  background: #fff;
  color: #f64f48;
  font-size: 0.7rem;
}
#indexCouponList .couponItem .text .couponType span .price .renmin {
  border: none;
  width: auto;
  background: #fff;
  color: #f64f48;
  font-size: 0.3rem;
}
#indexCouponList .couponItem .text .street {
  margin-top: 0.21rem;
  line-height: 0.5rem;
  height: 0.5rem;
  font-size: 0.28rem;
  color: #5f5f5f;
}
#indexCouponList .couponItem .text .street .street {
  display: inline-block;
}
#indexCouponList .couponItem .text .street .distance {
  float: right;
  line-height: 0.9rem;
  font-size: 0.28rem;
  color: #5f5f5f;
}
#indexCouponList .couponItem .text .usingTime {
  margin-top: 0.2rem;
  height: 0.38rem;
  font-size: 0.3rem;
  line-height: 0.3rem;
  border: 1px solid #f64f48;
  border-radius: 0.13rem;
  padding: 0.08rem;
  width: 11em;
  color: #f64f48;
}
#indexCouponList .couponItem .collect {
  position: absolute;
  top: 0.35rem;
  right: 0.77rem;
}
#indexCouponList .couponItem .collect .icon-collect {
  font-size: 0.66rem;
  position: absolute;
  top: 0;
  right: 0;
}
#indexCouponList .couponItem .collect .icon-collect-solid {
  color: #ff9c00;
  font-size: 0.66rem;
  position: absolute;
  top: 0;
  right: 0;
}
#indexCouponList .couponItem .collect .icon-spinner8 {
  position: absolute;
  top: 0;
  right: 0;
  color: #cecece;
  font-size: 0.56rem;
}
#indexCouponList .couponItem .rotate-enter-active {
  transition: -webkit-transform 3s linear;
  transition: transform 3s linear;
  transition: transform 3s linear, -webkit-transform 3s linear;
}
#indexCouponList .couponItem .rotate-enter {
  -webkit-transform: rotate(-1080deg);
          transform: rotate(-1080deg);
}
#indexCouponList .couponItem .btn {
  position: absolute;
  top: 1.245rem;
  right: 0.7rem;
  display: inline-block;
  height: 0.66rem;
  line-height: 0.66rem;
  width: 2.37rem;
  border-radius: 1rem;
  background: #6ccce5;
  color: #fff;
  border: none;
  outline: none;
}
#indexCouponList .couponItem .btn .btnName {
  margin: 0 auto;
  width: 4.5em;
  height: 100%;
  font-size: 0.36rem;
  color: #fff;
}
#indexCouponList .couponItem .btn .slide-fade-enter-active {
  transition: all 2s ease;
}
#indexCouponList .couponItem .btn .slide-fade-enter {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
}
#indexCouponList .couponItem .btnDisabled {
  background: #b5b5b5;
}
#indexCouponList .couponItem .btnDisabled .btnName {
  margin: 0 auto;
  width: 4em;
  height: 100%;
  font-size: 0.36rem;
  color: #fff;
}


