
.background {
  background-color: #f0f0f0;
  height: 100%;
  width: 100%;
}
.background .shoplist-item {
  position: relative;
  height: 2.8rem;
  line-height: 2.75rem;
  text-align: center;
  background: #fff;
  width: 10.8rem;
  border-bottom: 2px solid #f0f0f0;
}
.background .shoplist-item .shopDetail {
  display: inline-block;
  width: 100%;
  height: 100%;
}
.background .shoplist-item .shopDetail .shop-logo {
  position: absolute;
  top: 0.1rem;
  left: 0.18rem;
  height: 2.55rem;
  width: 2.55rem;
  border-radius: 6%;
  overflow: hidden;
}
.background .shoplist-item .shopDetail .shop-logo img {
  height: 100%;
}
.background .shoplist-item .shopDetail .shop-name {
  position: absolute;
  left: 3.05rem;
  top: 0.56rem;
  width: 7rem;
  font-size: 0.5rem;
  color: #212121;
  text-align: left;
  line-height: 1.2em;
  font-weight: bold;
}
.background .shoplist-item .collect {
  position: absolute;
  top: 0.56rem;
  right: 0.7rem;
}
.background .shoplist-item .icon-collect {
  font-size: 0.66rem;
  position: absolute;
  top: 0;
  right: 0;
}
.background .shoplist-item .icon-collect-solid {
  color: #ff9c00;
  font-size: 0.66rem;
  position: absolute;
  top: 0;
  right: 0;
}
.background .shoplist-item .icon-spinner8 {
  position: absolute;
  top: 0;
  right: 0;
  color: #cecece;
  font-size: 0.56rem;
}
.background .shoplist-item .rotate-enter-active {
  transition: -webkit-transform 3s linear;
  transition: transform 3s linear;
  transition: transform 3s linear, -webkit-transform 3s linear;
}
.background .shoplist-item .rotate-enter {
  -webkit-transform: rotate(-1080deg);
          transform: rotate(-1080deg);
}
.background .shoplist-item .shop-street {
  position: absolute;
  bottom: 0.36rem;
  left: 3.05rem;
  width: 7rem;
  height: 0.5rem;
  font-size: 0.3rem;
  text-align: left;
  line-height: 0.5rem;
  color: #5f5f5f;
}
.background .shoplist-item .shop-street .street {
  display: inline-block;
}
.background .shoplist-item .shop-street .distance {
  float: right;
  line-height: 0.5rem;
}


