
.couponList {
  width: 10.6rem;
  margin: 0 auto;
  overflow-x: hidden;
  background: #f0f0f0;
}
.couponList .couponItem {
  position: relative;
  background: #fff;
  margin: 0.1rem 0rem;
  height: 2.75rem;
  width: 10.8rem;
  padding-left: 0.24rem;
}
.couponList .couponItem .sawtooth {
  position: absolute;
  top: 0;
  left: 0rem;
  height: 2.75rem;
  width: 0.25rem;
  background: radial-gradient(#f0f0f0 0, #f0f0f0 0.19rem, #fff 0.19rem);
  background-size: 0.55rem 0.55rem;
  background-position: 0.29rem 0.55rem;
}
.couponList .couponItem .img {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0.3rem 0.3rem;
  height: 2.15rem;
  width: 2.63rem;
  overflow: hidden;
}
.couponList .couponItem .img img {
  height: 100%;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-origin: content-box;
}
.couponList .couponItem .text {
  position: absolute;
  top: 0;
  left: 3.13rem;
  display: inline-block;
  margin-left: 0.1rem;
  height: 2.75rem;
  width: 5rem;
  font-size: 0.5rem;
}
.couponList .couponItem .text .shopName {
  margin-top: 0.24rem;
  font-weight: bold;
  font-size: 0.5rem;
  line-height: 0.6667rem;
}
.couponList .couponItem .text .shopName .points {
  height: 0.6rem;
  line-height: 0.6rem;
}
.couponList .couponItem .text .shopName .distance {
  float: right;
  font-size: 0.28rem;
  color: #5f5f5f;
}
.couponList .couponItem .text .couponType {
  margin-top: 0.1rem;
  height: 0.4rem;
  color: #525252;
}
.couponList .couponItem .text .couponType span {
  display: inline-block;
  font-size: 0.3rem;
}
.couponList .couponItem .text .couponType span span {
  margin-right: 0.1rem;
  display: inline-block;
  width: 0.4rem;
  height: 0.4rem;
  line-height: 0.4rem;
  text-align: center;
  font-size: 0.24rem;
  background: #cecece;
  color: #fff;
  border-radius: 0.08rem;
}
.couponList .couponItem .text .couponType span .experience {
  background: #38e237;
}
.couponList .couponItem .text .couponType span .instead {
  background: #f64f48;
}
.couponList .couponItem .text .couponType span .discount {
  background: #ff9c00;
}
.couponList .couponItem .text .couponType span .function {
  background: #be61b2;
}
.couponList .couponItem .text .usingTime {
  margin-top: 0.3rem;
  height: 0.48rem;
  font-size: 0.3rem;
  line-height: 0.48rem;
  text-align: center;
  border: 1px solid #f64f48;
  border-radius: 0.13rem;
  width: 11em;
  color: #f64f48;
}
.couponList .couponItem .btn {
  position: absolute;
  top: 0;
  right: 0.25rem;
  display: inline-block;
  height: 2.75rem;
  line-height: 2.75rem;
  width: 2.29rem;
  background: #6ccce5;
  color: #fff;
  border: none;
  outline: none;
}
.couponList .couponItem .btn .btnName {
  margin: 0 auto;
  width: 4.5em;
  text-align: left;
  height: 100%;
  font-size: 0.36rem;
  color: #fff;
}
.couponList .couponItem .btn .btn-circle-right {
  position: absolute;
  left: 1.82rem;
  top: 0.935rem;
  width: 0.88rem;
  height: 0.88rem;
  border-radius: 50%;
  background: #f0f0f0;
}
.couponList .couponItem .btnDisabled {
  background: #b5b5b5;
}
.couponList .couponItem .btnDisabled .btnName {
  margin: 0 auto;
  width: 4em;
  height: 100%;
  font-size: 0.36rem;
  color: #fff;
}


