
#refund {
  width: 100%;
}
#refund .itemName {
  font-size: 0.44rem;
  line-height: 1.2rem;
  height: 1.2rem;
  padding-left: 0.4rem;
  background: #ebebeb;
  color: #6c6c6c;
}
#refund .item {
  display: block;
  height: 1.2rem;
  line-height: 1.2rem;
  padding-left: 0.88rem;
  font-size: 0.48rem;
}
#refund .item .refundMoney {
  display: inline-block;
  float: right;
  padding-right: 1rem;
  color: #f64f48;
  font-weight: bold;
}
#refund .coupons .function {
  padding-left: 1rem;
}
#refund .otherReason textarea {
  border: 1px #cecece solid;
  margin: 0.5rem 0.7rem;
  width: 9rem;
  height: 2rem;
  margin-bottom: 3.5rem;
}
#refund .refundCoupon {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.8rem;
  line-height: 2rem;
  text-align: center;
  background: #ebebeb;
}
#refund .refundCoupon .safe_warn {
  display: inline-block;
  font-size: 0.45rem;
}
#refund .refundCoupon .tips {
  display: inherit;
  margin-left: 0.5rem;
  margin-top: 0.3rem;
  font-size: 0.4rem;
  line-height: 0.5rem;
  text-align: left;
  height: 0.5rem;
}
#refund .refundCoupon .refundCouponBtn {
  font-size: 0.45rem;
  line-height: 1.4rem;
  height: 1.4rem;
  width: 10rem;
  margin: 0.2rem auto;
  margin-bottom: 0.2rem;
  border-radius: 0.1rem;
  color: #fff;
  background: #f64f48;
}


