
#gotoPay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f0f0f0;
}
#gotoPay .realPay {
  height: 4.2rem;
  width: 100%;
  margin-top: 1.44rem;
  background: #fff;
  color: #8b8b7a;
  line-height: 0.87rem;
}
#gotoPay .realPay .realPay1 {
  margin-top: 0.44rem;
  font-size: 0.48rem;
  display: inline-block;
  width: 100%;
  text-align: center;
}
#gotoPay .realPay .realPay2 {
  margin-top: 0.32rem;
  font-size: 0.89rem;
  display: inline-block;
  width: 100%;
  text-align: center;
  color: #f00;
  font-weight: bold /* 字体加粗 */;
}
#gotoPay .realPay .shopName {
  margin-top: 0.2rem;
  font-size: 0.41rem;
  display: inline-block;
  width: 100%;
  text-align: center;
  color: #3e3e3e;
}
#gotoPay .payWay {
  width: 100%;
  line-height: 1rem;
  color: #3e3e3e;
  background: #fff;
  text-align: left;
  margin-top: 0.2rem;
  padding-left: 0.6rem;
  font-size: 0.44rem;
}
#gotoPay .payWay .orderNbr {
  font-size: 0.35rem;
  color: #cecece;
}
#gotoPay .payWay .weixin {
  height: 2rem;
  line-height: 2rem;
  padding-left: 1rem;
}
#gotoPay .payWay .weixin .weixinIco {
  width: 0.7rem;
  height: 0.7rem;
}
#gotoPay .payWay .weixin .success {
  line-height: 2rem;
  float: right;
  margin-right: 1.5rem;
  font-size: 0.45rem;
}
#gotoPay .pay {
  position: fixed;
  bottom: 0;
  width: 10.8rem;
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  font-size: 0.5rem;
  background: #f64f48;
  color: #fff;
}


