
#orderdetails {
  color: #3d3d3d;
}
#orderdetails .consumeTime {
  width: 100%;
  height: 0.6rem;
  line-height: 0.6rem;
  text-align: right;
  margin-right: 0.1rem;
  background-color: #f3f3f3;
}
#orderdetails .shopName {
  line-height: 1rem;
  height: 1rem;
  text-indent: 0.4rem;
  font-size: 0.4rem;
  border-bottom: 2px solid #f3f3f3;
}
#orderdetails .orderNur {
  height: 0.7rem;
  line-height: 0.7rem;
  text-indent: 0.4rem /* 文字缩进 */;
}
#orderdetails .barcode .vue-barcode-element {
  height: 3rem;
  display: block;
  margin: 0 auto;
}
#orderdetails .orderAmount {
  border-top: 1px solid #f3f3f3;
  height: 1rem;
  width: 100%;
  line-height: 1rem;
  text-indent: 0.4rem /* 文字缩进 */;
  border-bottom: 1px solid #f3f3f3;
}
#orderdetails .orderAmount .Amount1 {
  float: left;
}
#orderdetails .orderAmount .Amount2 {
  float: right;
  margin-right: 0.4rem;
  font-weight: bold /* 字体加粗 */;
}
#orderdetails .fill {
  height: 0.3rem;
  background-color: #f3f3f3;
  width: 100%;
}
#orderdetails .status {
  height: 1rem;
  width: 100%;
  line-height: 1rem;
  text-indent: 0.4rem /* 文字缩进 */;
  border-bottom: 1px solid #f3f3f3;
}
#orderdetails .status .status2 {
  color: #adadad;
}
#orderdetails .deduction {
  height: 1rem;
  width: 100%;
  line-height: 1rem;
  text-indent: 0.4rem /* 文字缩进 */;
  border-bottom: 1px solid #f3f3f3;
}
#orderdetails .deduction .deduction1 {
  float: left;
}
#orderdetails .deduction .deduction2 {
  float: right;
  margin-right: 0.4rem;
  font-weight: bold /* 字体加粗 */;
}
#orderdetails .realPay {
  height: 1rem;
  width: 100%;
  line-height: 1rem;
  text-indent: 0.4rem /* 文字缩进 */;
  border-bottom: 1px solid #f3f3f3;
}
#orderdetails .realPay .realPay1 {
  float: left;
}
#orderdetails .realPay .realPay2 {
  float: right;
  margin-right: 0.4rem;
  font-weight: bold /* 字体加粗 */;
  color: #f64f48;
}
#orderdetails .paybox {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1.2rem;
  font-size: 0.44rem;
}
#orderdetails .paybox .cancel {
  float: right;
  display: inline-block;
  width: 2.5rem;
  height: 0.8rem;
  border: 1px solid #cecece;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.1rem;
  margin-right: 0.5rem;
}
#orderdetails .paybox .pay {
  float: right;
  display: inline-block;
  width: 2rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border: 1px solid #f64f48;
  border-radius: 0.1rem;
  color: #f64f48;
  margin-right: 0.5rem;
}
#header2 .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.24rem;
  line-height: 1.24rem;
  background: #f64f48;
  color: #fff;
}
#header2 .header .header-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
#header2 .header :nth-child(1) {
  font-size: 0.78rem;
  font-weight: bold;
  line-height: 1.24rem;
}
#header2 .header :nth-child(2) {
  -webkit-box-flex: 6;
      -ms-flex: 6;
          flex: 6;
  font-size: 0.5rem;
  line-height: 1.24rem;
  padding-right: 0.5rem;
}


