
#indexcoupondetails {
  background: #f0f0f0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}
.album {
  position: relative;
  width: 100%;
  height: 5rem;
  background-color: #f0f0f0;
  background-size: contain;
  text-align: center;
  line-height: 200px;
  overflow: hidden;
  z-index: 0;
}
.album .header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 1.28rem;
  width: 10.8rem;
  line-height: 1.28rem;
  color: #fff;
}
.album .header :nth-child(1) {
  float: left;
  margin-left: 0.3rem;
  margin-top: 0.15rem;
  width: 1.28rem;
  height: 1.28rem;
  line-height: 1.35rem;
  background: rgba(62,62,62,0.459);
  border-radius: 50%;
  font-size: 0.7rem;
  font-weight: bold;
}
.album .header :nth-child(2) span {
  float: right;
  color: #fff;
  margin-right: 0.3rem;
  margin-top: 0.15rem;
  width: 1.28rem;
  height: 1.28rem;
  line-height: 1.5rem;
  background: rgba(62,62,62,0.459);
  border-radius: 50%;
  text-align: center;
}
.album .header :nth-child(2) .icon-collect {
  font-size: 0.7rem;
  font-weight: bold;
  color: #fff;
}
.album .header :nth-child(2) .icon-collect-solid {
  font-size: 0.6rem;
  font-weight: bold;
  color: #ffda62;
}
.rule {
  width: 100%;
  margin-top: 0.1rem;
  padding: 0.4rem 0rem;
  line-height: 1rem;
  background: #fff;
}
.rule .rule-wrapper {
  margin: 0 auto;
  padding: 0.15rem 0.48rem;
  width: 8.8rem;
}
.rule .rule-wrapper .shopName {
  color: #7c7c7c;
  font-size: 0.44rem;
}
.rule .rule-wrapper .shopName .popularity {
  float: right;
}
.rule .rule-wrapper .ruleText {
  color: #f64f48;
  font-size: 0.72rem;
}
.rule .rule-wrapper .ruleText p {
  font-weight: bold;
}
.rule .rule-wrapper .discription {
  font-size: 0.44rem;
}
.barcode {
  margin-top: 0.1rem;
  width: 100%;
  background: #fff;
}
.barcontainer {
  width: 8rem;
  margin: 0 auto;
}
.barcontainer .vue-barcode-element {
  width: 8rem;
}
.shopInfoName {
  background: #fff;
}
.shopInfoName .name {
  margin: 0rem 0.5rem 0 0.5rem;
  padding: 0.5rem 0.4rem;
  font-size: 0.5rem;
  color: #525252;
  border-top: 1px #f0f0f0 solid;
}
.shopInfoName .name .icon-back {
  float: right;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.shopInfoName .name .icon-back:before {
  color: #525252;
}
.shopInfo {
  margin-top: 0.1rem;
  padding: 0.3rem 0.4rem;
  background: #fff;
  font-size: 0.4rem;
  color: #525252;
  position: relative;
}
.shopInfo .left {
  line-height: 0.7rem;
}
.shopInfo .left .clock {
  position: relative;
}
.shopInfo .left .clock .icon {
  position: absolute;
  display: inline-block;
  top: calc((100% - 0.5rem) / 2);
  width: 1rem;
}
.shopInfo .left .clock .clocktext {
  display: inline-block;
  width: 6rem;
  margin: auto 0;
  margin-left: 1rem;
}
.shopInfo .left .location {
  position: relative;
  margin-top: 0.18rem;
}
.shopInfo .left .location .icon {
  position: absolute;
  display: inline-block;
  top: calc((100% - 0.5rem) / 2);
  width: 1rem;
}
.shopInfo .left .location .locationtext {
  left: 1rem;
  width: 6rem;
  margin: auto 0;
  margin-left: 1rem;
  display: inline-block;
}
.shopInfo .right {
  position: absolute;
  right: 1.1rem;
  top: calc((100% - 0.5rem) / 2);
}
.shopInfo .right .content p {
  display: inline-block;
  padding-left: 0.9rem;
  border-left: solid 1px #343434;
}
.barcode {
  margin-top: 0.1rem;
  width: 100%;
  background: #fff;
}
.barcode .barwrapper {
  margin: 0 0.4rem;
  border-bottom: 1px solid #cecece;
}
.barcode .barwrapper .barcontainer {
  width: 7rem;
  margin: 0 auto;
  padding: 0.5rem 0;
}
.barcode .barwrapper .barcontainer .vue-barcode-element {
  width: 7rem;
  height: 4rem;
  border: 1px solid rgba(62,62,62,0.459);
  border-radius: 0.1rem;
}
.barcode .barcodeName {
  margin: 0.1rem 0.4rem;
  padding: 0.4rem 0.4rem;
  font-size: 0.5rem;
}
.barcode .barcodeName .icon-barcode {
  color: #000;
  font-size: 0.5rem;
  padding-right: 0.4rem;
}
.barcode .barcodeName .nouse {
  float: right;
  color: #149f14;
}
.barcode .barcodeName .used {
  float: right;
  color: #f64f48;
}
.barcode .barcodeName .disuse {
  float: right;
  color: #f64f48;
}
.barcode .isActive {
  background: rgba(232,232,232,0.522);
}
.shopdetail {
  margin-top: 0.1rem;
  background: #fff;
  font-size: 0.32rem;
  color: #525252;
}
.shopdetail .name {
  margin: 0rem auto;
  height: 1.3rem;
  line-height: 1.3rem;
  width: 4em;
  color: #525252;
  font-size: 0.4rem;
}
.shopdetail .text {
  height: 1.3rem;
  width: 8em;
  margin: 0 auto;
  font-size: 0.28rem;
}
.shopdetail .content {
  font-size: 0.32rem;
  line-height: 0.63rem;
  padding: 0.2rem 0.69rem;
  text-indent: 2em;
}
.cart {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 1.48rem;
  width: 100%;
  background: #fff;
}
.cart .btn {
  width: 100%;
  font-size: 0.44rem;
  color: #fff;
  background: #f64f48;
  text-align: center;
  line-height: 1.48rem;
}
.cart .price {
  color: #fff;
  background: #525252;
  width: 30%;
  float: left;
}
.cart .paybtn {
  width: 70%;
  float: right;
}
.cart .cantget {
  background: #b5b5b5;
}
.ruleInfo {
  background: #fff;
  width: 100%;
  margin-top: 0.1rem;
  line-height: 1rem;
  padding-bottom: 2rem;
}
.ruleInfo .ruleInfoName {
  margin: 0 auto;
  padding: 0.5rem;
  font-size: 0.5rem;
  width: 5em;
  text-align: center;
}
.ruleInfo .name {
  padding: 0 0.5rem;
  font-size: 0.44rem;
  font-weight: bold;
}
.ruleInfo .content {
  padding: 0 0.9rem;
  font-size: 0.4rem;
}
.ruleInfo .content:before {
  content: '• ';
  font-weight: bold;
}


