
#orderList {
  background-color: #f3f3f3;
}
#orderList .order {
  width: 100%;
  height: 4.5rem;
  font-size: 0.5rem;
  margin-bottom: 4px;
  color: #3d3d3d;
  background-color: #fff;
}
#orderList .order .orderHead {
  height: 1rem;
  font-size: 0.4rem;
  line-height: 1rem;
  color: #a9a9a9;
}
#orderList .order .orderHead .left {
  float: left;
  margin-left: 0.4rem;
}
#orderList .order .orderHead .right {
  float: right;
  margin-right: 0.6rem;
}
#orderList .order .orderBody {
  height: 2.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#orderList .order .orderBody .orderLogo {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
#orderList .order .orderBody .orderLogo .logo1 {
  height: 2rem;
  width: 2rem;
  display: block;
  margin: 0 auto;
}
#orderList .order .orderBody .orderLogo .logo1 img {
  height: 100%;
  width: 100%;
  border-radius: 6%;
}
#orderList .order .orderBody .orderDetial {
  -webkit-box-flex: 5;
      -ms-flex: 5;
          flex: 5;
  font-size: 0.35rem;
}
#orderList .order .orderBody .orderDetial .shopName {
  height: 50%;
  font-size: 0.5rem;
  line-height: 1rem;
}
#orderList .order .orderBody .orderDetial .orderTime {
  height: 50%;
  font-size: 0.35rem;
  line-height: 1rem;
  color: #a9a9a9;
}
#orderList .order .orderFoot {
  height: 1rem;
  font-size: 0.35rem;
  line-height: 1rem;
  margin-left: 0.4rem;
}
#orderList .order .orderFoot .foot1 {
  color: #a9a9a9;
}
#orderList .order .orderFoot .realPay {
  font-size: 0.37rem;
  color: #f64f48;
  font-weight: bold /* 字体加粗 */;
}


