
#submitorder {
  background: #f0f0f0;
}
#submitorder .order-item {
  width: 9.8rem;
  height: 1.5rem;
  line-height: 1.5rem;
  border-bottom: 1px solid rgba(206,206,206,0.376);
  padding: 0 0.5rem;
  background: #fff;
}
#submitorder .order-item label {
  font-size: 0.46rem;
  color: #3e3e3e;
}
#submitorder .order-item .inp {
  display: inline-block;
}
#submitorder .order-item .inp input {
  width: 1.5rem;
  text-align: center;
}
#submitorder .order-item .count-inp {
  display: inline;
  float: right;
}
#submitorder .order-item .count-inp .nbrPerPerson {
  border: none;
  color: #f64f48;
  font-size: 0.28rem;
  margin-right: 0.5rem;
}
#submitorder .order-item .count-inp .minus {
  display: inline;
  margin-left: 0rem;
  width: 0.5rem;
  height: 0.5rem;
  line-height: 0.5rem;
  font-size: 0.4rem;
  padding: 0.1rem 0.38rem;
  border: 1px solid #f64f48;
  border-radius: 10%;
}
#submitorder .order-item .count-inp .minus-disabled {
  border-color: #cecece;
}
#submitorder .order-item .count-inp .add {
  display: inline;
  width: 0.5rem;
  height: 0.5rem;
  font-size: 0.4rem;
  padding: 0.1rem 0.3rem;
  border: 1px solid #f64f48;
  border-radius: 10%;
}
#submitorder .order-item .totalPrice {
  float: right;
  font-size: 0.55rem;
  font-weight: bold;
  color: #f64f48;
  display: inline-block;
}
#submitorder .order-item-more {
  position: relative;
  height: 3rem;
  margin-bottom: 0.3rem;
}
#submitorder .order-item-more .img {
  display: inline-block;
  margin-top: 0.5rem;
  height: 2rem;
  width: 3rem;
  overflow: hidden;
}
#submitorder .order-item-more .img img {
  height: 100%;
  width: auto;
}
#submitorder .order-item-more .orderName {
  display: inline-block;
  position: absolute;
  top: 0.4rem;
  left: 4rem;
  width: 6rem;
  line-height: 0.7rem;
  font-size: 0.48rem;
  font-weight: bold;
}
#submitorder .order-item-more .payPrice {
  display: inline;
  position: absolute;
  right: 2rem;
  bottom: 0rem;
  font-size: 0.44rem;
  font-weight: bold;
  color: #f64f48;
}
#submitorder .pay {
  position: fixed;
  bottom: 0;
  width: 10.8rem;
  height: 1.5rem;
  line-height: 1.5rem;
  text-align: center;
  font-size: 0.5rem;
  background: #f64f48;
  color: #fff;
}


