
#myinformation {
  width: 100%;
  overflow: hidden;
}
.personalInfo {
  position: relative;
  width: 100%;
  height: 7.5rem;
  text-align: center;
}
.personalInfo .circle {
  position: absolute;
  bottom: 1rem;
  left: 0;
  z-index: -1;
}
.personalInfo .circle .circle1 {
  position: absolute;
  bottom: 0;
  left: -3.465rem;
  width: 17.73rem;
  height: 12.68rem;
  background: #f64f48;
  opacity: 1;
  border-radius: 50%;
}
.personalInfo .circle .circle2 {
  position: absolute;
  bottom: 0.19rem;
  left: -0.21rem;
  width: 11.22rem;
  height: 11.22rem;
  background: #fff;
  border-radius: 50%;
  opacity: 1;
}
.personalInfo .circle .circle3 {
  position: absolute;
  bottom: -2.7rem;
  left: -7.465rem;
  width: 25.73rem;
  height: 12.68rem;
  z-index: -1;
  border-radius: 50%;
  background: -webkit-radial-gradient(50% 50%, farthest-corner, #f00, #fff, #fff);
}
.personalInfo .img {
  margin: 0.6rem auto;
  width: 2.89rem;
  height: 2.89rem;
  line-height: 2.89rem;
  border: 0px solid rgba(246,79,72,0.502);
  border-radius: 100%;
  text-align: center;
  background: #fff;
  overflow: hidden;
}
.personalInfo .img img {
  height: 100%;
  width: 100%;
}
.personalInfo .weixinId {
  font-size: 0.48rem;
  line-height: 1rem;
  font-weight: bold;
  color: #0d2949;
}
.personalInfo .mobile {
  font-size: 0.4rem;
  color: #a2aab4;
}
.personalInfo .login {
  color: #fff;
  padding: 0.1rem 0.5rem;
  background: #f64f48;
  border: #f64f48 1px solid;
  border-radius: 0.05rem;
}
.information {
  font-size: 0.5rem;
  margin-bottom: 50px;
}
.information .infoItem {
  width: 8.5rem;
  height: 1.4rem;
  margin: 0 auto;
  line-height: 1.4rem;
  text-align: left;
}
.information .infoItem .arrow {
  float: right;
  font-size: 20px;
  line-height: 1.4rem;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
#changeAvatar {
  z-index: -100 !important;
  visibility: hidden;
}
.prompt {
  position: absolute;
  top: 3.5rem;
  border: 1px solid #dbdbdb;
  border-radius: 5px;
  height: 0.75rem;
  line-height: 0.75rem;
  left: 6rem;
  color: #dbdbdb;
}
.showPrompt {
  box-shadow: 2px 2px 2px 2px #888;
}


