
#mycollection {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #f0f0f0;
  padding-bottom: 0.1rem;
}
#mycollection .couponTabs {
  position: fixed;
  top: 1.2rem;
  left: 0rem;
  width: 100%;
  height: 1.2rem;
  line-height: 1.2rem;
  text-align: center;
  font-size: 0.4rem;
  background: #f64f48;
  color: #fff;
  z-index: 1;
}
#mycollection .couponTabs li {
  width: 5.38rem;
  float: left;
}
#mycollection .couponTabs li.active {
  position: relative;
  font-size: 0.48rem;
  font-weight: bold;
}
#mycollection .couponTabs li.active .rec {
  position: absolute;
  bottom: -0.04rem;
  left: calc((100% - 0.46rem) / 2);
  width: 0;
  height: 0;
  border: 0.23rem solid transparent;
  border-bottom: 0.23rem solid #f0f0f0;
}
#mycollection .fade-enter-active {
  transition: opacity 0.5s;
}
#mycollection .fade-enter {
  opacity: 0;
}
#mycollection .shoplist {
  margin-top: 2.5rem;
}
#mycollection .shoplist .shoplist-empty {
  position: absolute;
  top: 50%;
  left: calc((100% - 11em) / 2);
  color: #a7a7a7;
  font-size: 0.45rem;
}
#mycollection .couponlist {
  margin-top: 2.5rem;
}
#mycollection .couponlist .shoplist-empty {
  position: absolute;
  top: 50%;
  left: calc((100% - 11em) / 2);
  color: #a7a7a7;
  font-size: 0.45rem;
}


