
#indexCity {
  background: #f0f0f0;
}
#indexCity .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.24rem;
  line-height: 1.24rem;
  background: #f64f48;
}
#indexCity .header .header-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 1.24rem;
  line-height: 1.24rem;
  text-align: center;
  vertical-align: middle;
}
#indexCity .header :nth-child(1) {
  color: #fff;
  height: 1.24rem;
  line-height: 1.24rem;
  font-size: 0.66rem;
  font-weight: bold;
}
#indexCity .header :nth-child(2) {
  color: #fff;
  -webkit-box-flex: 5;
      -ms-flex: 5;
          flex: 5;
  height: 1.24rem;
  line-height: 1.24rem;
  font-size: 0.45rem;
  text-align: center;
}
#indexCity .header :nth-child(2) .headInput {
  background-color: #eaeaea;
  vertical-align: middle;
  text-indent: 0.4rem;
/*文本缩进的距离*/
  height: 0.7rem;
  font-size: 0.44rem;
  color: #b3b3b3;
  font-family: Georgia;
  width: 5.4rem;
  margin-bottom: 4px;
}
#indexCity .city {
  width: 10.8rem;
  background-color: #fff;
}
#indexCity .city .changeCity {
  margin: 0;
  height: 1.3rem;
  font-size: 0.44rem;
  line-height: 1.3rem;
  text-indent: 0.6rem;
  border-bottom: 1px solid #cecece;
}
#indexCity .city .activeCity {
  color: #f64f48;
  background: rgba(232,232,232,0.522);
}


