
.column {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 10.8rem;
  height: 2.1rem;
  background: #fff;
}
.column .text {
  width: 10.8rem;
  text-align: center;
  line-height: 2.1rem;
}
.column .column-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 2.1rem;
  margin-top: 0.2rem;
  text-align: center;
}
.column .column-item .columnImg {
  height: 50%;
  width: 100%;
}
.column .column-item .columnImg img {
  height: 100%;
}
.column .column-item .columnText {
  line-height: 0.75rem;
  font-size: 0.42rem;
  color: #000;
}


