
#nav .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 1.52rem;
  line-height: 1.52rem;
  position: relative;
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: #fff;
}
#nav .tab:before {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  border-top: 1px solid rgba(181,181,181,0.3);
  content: ' ';
}
#nav .tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  line-height: 1.52rem;
}
#nav .tab .tab-item > a {
  display: block;
  height: 0.36rem;
  line-height: 0.35rem;
  font-size: 0.28rem;
  color: #4d555d;
}
#nav .tab .tab-item > a:before {
  display: inline-block;
  height: 0.65rem;
  line-height: 1.3rem;
  font-size: 0.7rem;
  width: 100%;
}
#nav .tab .tab-item > a.active {
  color: #f01414;
}


