
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
#login {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
#login .mask {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(17,17,17,0.7);
}
#login .table {
  position: initial;
  width: 70%;
  margin: 0 auto;
  margin-top: 18%;
  padding: 0.5rem;
  padding-bottom: 1.8rem;
  font-size: 0.3rem;
  background: #fff;
  border-radius: 5px;
}
#login .table .img {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 auto;
  margin-bottom: 0.5rem;
}
#login .table .img img {
  height: 100%;
}
#login .table p {
  width: 100%;
  margin-bottom: 0.4rem;
  padding-left: 0.5rem;
  height: 1.2rem;
  width: calc(100% - 0.5rem);
  border-radius: 0.1rem;
  border: 1px solid #cecece;
  font-size: 0.48rem;
}
#login .table p input {
  height: 1.2rem;
  width: 100%;
  border-radius: 0.1rem;
  font-size: 0.48rem;
  outline: none;
}
#login .table p .vcodeInp {
  width: 3.8rem;
}
#login .table p .vcode {
  display: inline-block;
  width: 3rem;
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  margin: auto 0;
  margin-bottom: 0.15rem;
  vertical-align: middle;
  background: #f64f48;
  border: #f64f48;
  border-radius: 0.1rem;
  color: #fff;
  font-size: 0.28rem;
}
#login .table p .disabled {
  background: #aaa;
  border: none;
}
#login .table .loginbtn {
  display: inline-block;
  margin: 0.1rem auto;
  margin-right: calc((100% - 7.2rem));
  width: 1.6rem;
  text-align: center;
  font-size: 0.5rem;
  padding: 0.3rem 0.9rem;
  color: #fff;
  background: #f64f48;
  border: none;
  border-radius: 5px;
}
#login .table .cancel {
  display: inline-block;
  width: 1.6rem;
  text-align: center;
  margin: 0.1rem auto;
  font-size: 0.5rem;
  padding: 0.3rem 0.9rem;
  color: #fff;
  background: #aaa;
  border: none;
  border-radius: 5px;
}
#login .table .toRegister {
  margin-top: 0.8rem;
  float: right;
  font-size: 0.35rem;
  color: #f64f48;
  text-decoration: underline;
}


