
.swiper {
  margin-top: 1.24rem;
  height: 4.4rem;
  width: 10.8rem;
  text-align: center;
  background-color: #333;
}
.swiper .swiper-container {
  height: 100%;
  z-index: 0;
}
.swiper .swiper-container .swiper-wrapper {
  height: 100%;
}
.swiper .swiper-container .swiper-wrapper img {
  width: 100%;
}
.swiper .swiper-container .swiper-pagination {
  height: 0.2rem;
}
.swiper .container {
  height: 3.4rem;
  width: 10.8rem;
  overflow: hidden;
  position: absolute;
  left: 0;
  top: 1.24rem;
}
.swiper .container img {
  width: 100%;
  overflow: hidden;
}


