
#order .change {
  position: fixed;
  top: 1.24rem;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 1.5rem;
  border-bottom: 0.1rem solid #f3f3f3;
  background-color: #fff;
  margin: 0 auto;
}
#order .change .change0 {
  position: absolute;
  top: 0.24rem;
  left: 25%;
  height: 1rem;
  width: 5rem;
}
#order .change .change0 .finished {
  float: left;
  width: 49%;
  text-align: center;
  line-height: 1rem;
  border: solid #f64f48 1px;
  border-right: none;
  background-color: #f64f48;
  color: #f3f3f3;
  font-size: 0.35rem;
  border-radius: 5px 0px 0px 5px;
}
#order .change .change0 .unfinished {
  float: right;
  width: 49.5%;
  text-align: center;
  line-height: 1rem;
  color: #f64f48;
  font-size: 0.35rem;
  border: solid #f64f48 1px;
  border-left: none;
  border-radius: 0px 5px 5px 0px;
}
#order .orderList {
  position: absolute;
  top: 2.89rem;
  left: 0;
  width: 100%;
}
.info {
  position: absolute;
  top: 3rem;
  left: 0;
  width: 96%;
  text-align: center;
  height: 1.5rem;
  margin-left: 2%;
  line-height: 1.5rem;
  border: solid 1px #cdcdc1;
  font-size: 0.45rem;
  color: #cdcdc1;
  z-index: 3;
}
.isFinished {
  background-color: #f64f48 !important;
  color: #fff !important;
}
.isUnfinished {
  background-color: #fff !important;
  color: #f64f48 !important;
}


