
.barcode {
  margin-top: 0.1rem;
  width: 100%;
  background: #fff;
}
.barcode .use {
  margin: 0.1rem 0.4rem;
  padding: 0.4rem 0.4rem;
  font-size: 0.42rem;
}
.barcode .use .refundbtn {
  float: right;
  color: #f64f48;
}
.barcode .use .tips {
  color: #cecece;
  margin-top: 0.2rem;
  font-size: 0.4rem;
}
.barcode .barwrapper {
  margin: 0 0.4rem;
  border-bottom: 1px solid #cecece;
}
.barcode .barwrapper .barcontainer {
  width: 7rem;
  margin: 0 auto;
  padding: 0.5rem 0;
}
.barcode .barwrapper .barcontainer .vue-barcode-element {
  width: 7rem;
  height: 4rem;
  border: 1px solid rgba(62,62,62,0.459);
  border-radius: 0.1rem;
}
.barcode .userCouponNbrList {
  position: relative;
}
.barcode .userCouponNbrList .barcodeName {
  margin: 0.1rem 0.4rem;
  padding: 0.4rem 0.4rem;
  font-size: 0.5rem;
}
.barcode .userCouponNbrList .barcodeName .icon-barcode {
  color: #000;
  font-size: 0.5rem;
  padding-right: 0.4rem;
}
.barcode .userCouponNbrList .barcodeName .nouse {
  float: right;
  color: #149f14;
  padding-right: 1.5rem;
}
.barcode .userCouponNbrList .barcodeName .used {
  float: right;
  color: #f64f48;
  padding-right: 1.5rem;
}
.barcode .userCouponNbrList .barcodeName .disuse {
  float: right;
  color: #f64f48;
  padding-right: 1.5rem;
}
.barcode .userCouponNbrList .addbtn {
  position: absolute;
  right: 0.5rem;
  top: 0.23rem;
  width: 2.3rem;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  border-radius: 0.1rem;
  background: #f64f48;
  color: #fff;
}
.barcode .userCouponNbrList .addbtn .icon-spinner8 {
  position: absolute;
  top: 0.2rem;
  left: 0.9rem;
  font-size: 0.46rem;
}
.barcode .userCouponNbrList .addbtn .rotate-enter-active {
  transition: -webkit-transform 4s linear;
  transition: transform 4s linear;
  transition: transform 4s linear, -webkit-transform 4s linear;
}
.barcode .userCouponNbrList .addbtn .rotate-enter {
  -webkit-transform: rotate(-1440deg);
          transform: rotate(-1440deg);
}
.barcode .userCouponNbrList .isActive {
  background: rgba(232,232,232,0.522);
}
.barcode .more {
  position: relative;
  width: 13.5em;
  margin: 0.1rem auto;
  padding: 0.4rem 0.4rem;
  font-size: 0.42rem;
}
.barcode .more .icon-more {
  position: absolute;
  top: 0.32rem;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.barcode .more .icon-more-y {
  position: absolute;
  top: 0.5rem;
}


