
#myCoupon {
  position: absolute;
  height: 100%;
  width: 10.8rem;
  background: #f0f0f0;
}
#myCoupon .couponTabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 1.68rem;
  line-height: 1.68rem;
  text-align: center;
  font-size: 0.4rem;
  background: #f64f48;
  color: #fff;
}
#myCoupon .couponTabs li {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
#myCoupon .couponTabs li.active {
  position: relative;
  font-size: 0.48rem;
  font-weight: bold;
}
#myCoupon .couponTabs li.active .rec {
  position: absolute;
  bottom: -0.01rem;
  left: calc((100% - 0.46rem) / 2);
  width: 0;
  height: 0;
  border: 0.23rem solid transparent;
  border-bottom: 0.23rem solid #f0f0f0;
}
#myCoupon .usercoupon-enter-active,
#myCoupon .usercoupon-leave-active {
  transition: opacity 2s;
}
#myCoupon .usercoupon-enter,
#myCoupon .usercoupon-leave-to {
  opacity: 0;
}
#myCoupon .userCouponList {
  margin-bottom: 1.5rem;
}
#myCoupon .coupon-empty {
  position: absolute;
  top: 50%;
  left: calc((100% - 5.1em) / 2);
  color: #a7a7a7;
  font-size: 0.45rem;
}
#myCoupon .background-text {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 0.45rem;
  background: #f3f3f3;
  color: #a7a7a7;
}
#myCoupon .background-text .login-back {
  margin: 1rem auto;
  margin-top: calc((100% -1em) / 2);
  margin-left: calc((100% - 3.8rem) / 2);
}
#myCoupon .background-text .login-back .login {
  color: #fff;
  padding: 0.1rem 0.5rem;
  background: #f64f48;
  border: #f64f48 1px solid;
  border-radius: 0.05rem;
}
#myCoupon .barcode .mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(91,91,91,0.55);
  z-index: 1;
}
#myCoupon .barcode .code {
  position: fixed;
  top: 2rem;
  left: calc((100% - 9rem) / 2);
  padding: 0.7rem 1rem;
  background: #fff;
  border-radius: 0.1rem;
  z-index: 2;
}
#myCoupon .barcode .code .vue-barcode-element {
  width: 7rem;
  height: 4.5rem;
}
#myCoupon .barcode .code .text {
  font-size: 0.44rem;
}
#myCoupon .barcode .code .tips {
  width: 7rem;
  margin: 1rem 0;
  font-size: 0.34rem;
}
#myCoupon .barcode .code .btn button {
  width: 100%;
  height: 1rem;
  background: #f64f48;
  border: #f64f48;
  border-radius: 0.1rem;
  color: #fff;
}


