
#header2fix .header {
  width: 100%;
  height: 1.24rem;
  background-color: #f64f48;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}
#header2fix .header .header-item {
  text-align: center;
  color: #fff;
}
#header2fix .header :nth-child(1) {
  display: inline-block;
  height: 1.24rem;
  line-height: 1.24rem;
  width: 1.5rem;
  font-size: 28px;
  font-weight: bold;
}
#header2fix .header :nth-child(2) {
  display: inline-block;
  position: absolute;
  left: 2.2rem;
  height: 1.24rem;
  line-height: 1.24rem;
  font-size: 0.5rem;
  width: 6.3rem;
}


